

.dark {
    background-color: var(--dark-body-color);
    color: var(--accent-color3);
}

body.dark {
    background-color: var(--dark-theme-background);
    color: var(--accent-color3);
}

// ---------- header -------------------------------//
//{
    body.dark .header {
        background-color: var(--text-dark-color);
        color: var(--accent-color3);
        border: 1.5px solid #ffffff;
    }

    body.dark .logo-link,
    body.dark .home-link,
    body.dark .shopping-link,
    body.dark .shopping-link .shopping-bag-icon,
    body.dark .burger-logo {
        color: var(--accent-color3);
        fill: var(--accent-color3);
        stroke: var(--accent-color3);
    }

    body.dark .shopping-link:hover,
    body.dark .shopping-link:focus,
    body.dark .shopping-link:hover .shopping-bag-icon,
    body.dark .shopping-link:focus .shopping-bag-icon,
    body.dark .home-link:hover,
    body.dark .home-link:focus{
        color: var(--text-dark-color);
        fill: var(--text-dark-color);
        stroke: var(--text-dark-color);

    }
    body.dark .home-link.selected, 
    body.dark .shopping-link.selected,
    body.dark .shopping-link.selected .shopping-bag-icon {
        color: var(--text-dark-color);
        fill: var(--text-dark-color);
        stroke: var(--text-dark-color);
    }
//}

//----------- list_of_categories -------------------//
//{
    body.dark .category {
        color: rgba(255, 255, 255, 0.6);
    }

    body.dark .category:hover,
    body.dark .category:focus {
        color: var(--accent-color2);
    }

    body.dark .category-title,
    body.dark .book-name,
    body.dark .button-see-more {
        color: var(--accent-color3);
    }

    body.dark .title-theme-book {
        color: var(--accent-color3);
    }

    body.dark .title-book {
        color: var(--accent-color3);

    }

    body.dark .button-more {
        color: var(--text-white-color);
    }

    body.dark #category-list-title {
        color: var(--accent-color2);
    
    }

    body.dark .category-list-item{
        color: rgba(255,255,255,0.6);
    }

    body.dark .category-list-item:hover{
        color: var(--accent-color2);
        text-transform: uppercase;
    }

    body.dark .category-list-box::-webkit-scrollbar{
        background-color: var(--scrollbar-dark-color);
    }

    body.dark .category-list-box::-webkit-scrollbar-thumb {
        background-color: var(--scrollbar-dark-color);
        box-shadow: inset 1px 1px 10px #797a74;
    }
//}

// ---------- autorization_modal_window ------------//
//{
    body.dark .modal{
        background-color: black;
        border-color: var(--accent-color3);
        // border-radius: 18px;
        // border: #ffffff;
    }

    body.dark .close-button {
        color: var(--accent-color3)}

    body.dark .modalForm-inputWrap{
        --auth-icon-color: var(--auth-icon-color3);  
    }
    body.dark .modalForm-input {
        border: 2px solid var(--text-white-color);
        background-color: transparent;
        color: var(--text-white-color);
    }
    body.dark .modalForm-input:focus {
        border: 2px solid var(--accent-color1);
        outline: none;
        border-color: var(--accent-color1);
        color: var(--accent-color1);
      }
    body.dark.modalForm-input:focus+ .modalForm-icon{
        --auth-icon-color: var(--auth-icon-color2);
      }

    body.dark .modalForm-input::placeholder {
        color: currentColor;
    }

    body.dark .close-icon,
    body.dark .close-button
    {
        fill: #ffffff;
    }

    body.dark .modalForm-icon.email{
        fill: #ffffff;
    }

    body.dark .submitBtn{
        color:black;
        background-color: var(--accent-color3);
        &:hover,
        &:focus {
            background-color: var(--accent-color1);
            border: 2px solid var(--text-dark-color);
            color: var(--text-white-color);
        }
    }

    body.dark .btn-sign-up,
    body.dark .btn-sign-in{
        color: rgba(255,255,255, 0.6);
        &:hover,
        &:focus {
          color: var(--accent-color1);
        }
        &.current {
            color: var(--accent-color1);
          }
    }


//}

//----------- shopping_list ------------------------//
//{
    body.dark .shopping_list_title,
    body.dark .book-title {
        color: var(--accent-color3);
    }

    body.dark .book-description{
        color: var(--text-white-color);
    }

    body.dark #amazon-icon{
        --amazon-path1-color: var(--text-white-color);
        --amazon-path2-color: var(--text-white-color);
    }

    body.dark .pgn-btn{
        color: var(--text-white-color);
        border: 1px solid var(--text-white-color);
        --pgn-fill-color: var(--text-white-color);
    }
    body.dark .pgn-btn:hover,
    body.dark .pgn-btn:focus {
        color: var(--text-dark-color);
        background-color: var(--accent-color2);
        border: 1px solid var(--text-dark-color);
        --pgn-fill-color: var(--text-dark-color);
    }
    body.dark .pgn-btn.active {
        color: var(--text-dark-color);
        background-color: var(--text-white-color);
    }
    body.dark .pgn-btn:disabled{
        border: 1px solid var(--text-dark-color);
        background-color: var(--text-dark-color);
        --pgn-fill-color: var(--text-light-color);
    }

    body.dark .empty-shopping-box-text{
        color: rgba(255, 255, 255, 0.6);
    }
//}

//----------- loader -------------------------------//
//{
    body.dark .loader{
        --color1:var(--accent-color3);
        --color2:var(--accent-color1);
    }
//}

//----------- book modal window --------------------//
//{
    body.dark .book-modal-container{
        background-color: var(--dark-theme-background);
        border-color: var(--text-white-color);
    }
    
    body.dark .icon-x {
        stroke: var(--text-white-color);
        fill: var(--text-white-color);
    }
    body.dark .icon-x:hover,
    body.dark .icon-x:focus {
        stroke: var(--accent-color1);
        fill: var(--accent-color1);
    }

    body.dark .modal-name-book,
    body.dark .modal-description-book{
        color: var(--text-white-color);
    }
    body.dark .modal-author-book{
        color: var(--text-light-color);
    }
    body.dark .modal-message{
        color: var(--text-white-color-fifty);
    }
    
    body.dark .btn-shopping {
        background-color: var(--dark-theme-background);
        color: var(--text-white-color);
        border: 2px solid var(--accent-color1);
    }
    body.dark .btn-shopping:focus,
    body.dark .btn-shopping:hover {
        color: var(--text-white-color);
        background-color: var(--accent-color1);
    }

    body.dark .marketplacer_li_one .marketplacer_li_link{
        --amazon-icon-path1-color: var(--accent-color1);
        --amazon-icon-path2-color: var(--accent-color1);
    }
//}